$main: #201c24;
$main-dark: darken($main, 5%);
$main-light: lighten($main, 7.5%);
$accent: #34d17d;
$accent-dark: linear-gradient(to right, desaturate(darken($accent, 5%), 10%), desaturate(darken($accent, 5%), 10%));
$underline: linear-gradient(to right, $accent, rgba($accent, 0));
$text1: white;
$text2: white;